import React from 'react';

import CSSTransitionGroup from 'react-addons-css-transition-group';

import {formatPrice} from '../helpers';

class Order extends React.Component {
  constructor() {
    super()
    this.renderOrder = this.renderOrder.bind(this);
  }

  renderOrder(fishKey) {
    const fish = this.props.fishes[fishKey];
    const quantity = this.props.order[fishKey];
    const removeButton = <button onClick={() => this.props.removeFromOrder(fishKey)}>&times;</button>

    if(!fish || fish.status === 'unavailable') {
      return (
        <li key={fishKey}>
          Sorry, {fish ? fish.name : 'fish'} is no longer available!
          {removeButton}
        </li>
      );
    }

    return (
      <li key={fishKey}>
        <span>
          <CSSTransitionGroup
            component="span"
            className="count"
            transitionName="count"
            transitionEnterTimeout={200}
            transitionLeaveTimeout={200}>
            <span key={quantity}>{quantity}</span>
          </CSSTransitionGroup>
          lbs {fish.name} {removeButton}
        </span>
        <span className="price">{formatPrice(quantity * fish.price)}</span>
      </li>
    );
  }

  render() {
    const orderIds = Object.keys(this.props.order);
    const total = orderIds.reduce((orderTotal, fishId) => {
      const fish = this.props.fishes[fishId] || {};
      const fishPrice = fish.price || 0;
      const quantity = this.props.order[fishId] || 0;
      const isAvailable = fish.status === "available";
      if (isAvailable)
        return orderTotal + fishPrice * quantity;
      else
        return orderTotal;
    }, 0);
    return (
      <div className="order-wrap">
        <h2>Your Order</h2>
        <CSSTransitionGroup
          className="order"
          component="ul"
          transitionName="order"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={500}>
          {orderIds.map(this.renderOrder)}
          <li className="total">
            <strong>Total:</strong>
            {formatPrice(total)}
          </li>
        </CSSTransitionGroup>
      </div>
    )
  }
}

Order.propTypes = {
  fishes: React.PropTypes.object.isRequired,
  order: React.PropTypes.object.isRequired,
  removeFromOrder: React.PropTypes.func.isRequired
};

export default Order;



