import React from 'react';

import AddFishForm from './AddFishForm';
import base from '../base';

class Inventory extends React.Component {
  constructor() {
    super();
    this.renderInventory = this.renderInventory.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.renderLogin = this.renderLogin.bind(this);
    this.authenticate = this.authenticate.bind(this);
    this.logout = this.logout.bind(this);
    this.authHandler = this.authHandler.bind(this);
    this.state = {
      uid: null,
      owner: null
    };
  }

  componentDidMount() {
    base.onAuth(user => {
      if(user) {
        this.authHandler(null, { user })
      }
    });
  }

  handleChange(e, fishKey) {
    const fish = this.props.fishes[fishKey];
    const updatedFish = {
      ...fish,
      [e.target.name]: e.target.value
    };
    this.props.updateFish(fishKey, updatedFish);
  }

  authenticate(provider) {
    base.authWithOAuthPopup(provider, this.authHandler);
  }

  logout() {
    base.unauth();
    this.setState({
      uid: null
    });
  }

  authHandler(err, authData) {
    if(err) {
      console.error(err);
      return;
    }

    const storeRef = base.database().ref(this.props.storeId);
    storeRef.once('value', snapshot => {
      const data = snapshot.val() || {};
      if(!data.owner) {
        storeRef.set({
          owner: authData.user.uid
        });
      }
      this.setState({
        uid: authData.user.uid,
        owner: data.owner || authData.user.uid
      });
    });
  }

  renderLogin() {
    return (
      <nav className="login">
        <h2>Inventory</h2>
        <p>Sign in to manage store's inventory</p>
        <button className="github" onClick={() => this.authenticate('github')}>
          Log In with Github
        </button>
        <button className="facebook" onClick={() => this.authenticate('facebook')}>
          Log In with Facebook
        </button>
        <button className="twitter" onClick={() => this.authenticate('twitter')}>
          Log In with Twitter
        </button>
      </nav>
    )
  }

  renderInventory(fishKey) {
    const fish = this.props.fishes[fishKey];
    return (
      <div className="fish-edit" key={fishKey}>
        <input name="name" onChange={(e) => this.handleChange(e, fishKey)} value={fish.name}
          type="text" placeholder="Fish Name"/>
        <input name="price" onChange={(e) => this.handleChange(e, fishKey)} value={fish.price} type="text" placeholder="Fish Price"/>
        <select name="status" onChange={(e) => this.handleChange(e, fishKey)} value={fish.status}>
          <option value="available">Fresh!</option>
          <option value="unavailable">Sold Out!</option>
        </select>
        <textarea name="desc" onChange={(e) => this.handleChange(e, fishKey)} value={fish.desc} type="text" placeholder="Fish Desc"/>
        <input name="image" onChange={(e) => this.handleChange(e, fishKey)} value={fish.image} type="text" placeholder="Fish Image"/>
        <button onClick={() => this.props.removeFish(fishKey)}>Remove Fish</button>
      </div>
    );
  }

  render() {
    const logout = <button onClick={this.logout}>Log Out!</button>;

    if(!this.state.uid) {
      return <div>{this.renderLogin()}</div>
    }

    if(this.state.uid !== this.state.owner) {
      return (
        <div>
          <p>Sorry, you aren't the owner of the store!</p>
          {logout}
        </div>
      )
    }

    return (
      <div>
        <h2>Inventory</h2>
        {logout}
        {Object.keys(this.props.fishes).map(this.renderInventory)}
        <AddFishForm addFish={this.props.addFish} />
        <button onClick={this.props.loadSamples}>Load Sample Fishes</button>
      </div>
    )
  }
}

Inventory.propTypes = {
  fishes: React.PropTypes.object.isRequired,
  addFish: React.PropTypes.func.isRequired,
  updateFish: React.PropTypes.func.isRequired,
  removeFish: React.PropTypes.func.isRequired,
  loadSamples: React.PropTypes.func.isRequired,
  storeId: React.PropTypes.string.isRequired
};

export default Inventory;



